<template>
  <div id="app">
    <router-view></router-view>
    <div class="meiyoul1" v-show="$route.meta.showFooter"></div>
    <div class="nav" v-show="$route.meta.showFooter">
      <router-link to="/publish" class="Bar-tar">
        <div class="Icon-Zixun Icon"></div>
        <div>首页</div>
      </router-link>
      <router-link to="/analysis" class="Bar-tar">
        <div class="Icon-Shengcheng Icon"></div>
        <div>数据分析</div>
      </router-link>
      <router-link to="/bill" class="Bar-tar GouWuChe">
        <div class="Icon-MyIndex Icon"></div>
        <div>账单</div>
        <div class="YUan" v-if="$store.state.getCartNum">{{$store.state.getCartNum}}</div>
      </router-link>
      <router-link to="/Personal/index" class="Bar-tar">
        <div class="Icon-Shangmai Icon"></div>
        <div>我的</div>
      </router-link>
    </div>
     <div class="open-haibao" v-if="opentime">
			<div class="open-time" v-text="opentime" @click="tiaoguo">
				5
			</div>
			<img src="./views/yu/assets/openhaibao.jpg" />
		</div>
  </div>
</template>
<script>
  import {build_num,down_url,platform} from "../project";
export default {

     data() {
        return {
           opentime:0,
            int:''
        };
      },
      mounted() {
        if(process.env.VUE_APP_API_Name == '易品'){
          //判断是否易品项目
          let self = this;
          self.opentime = 3;
          self.int = setInterval(function() {
            console.log(self.opentime)
            self.opentime -=1;
            if(self.opentime  == 0){
              clearInterval(self.int);
            }
          }, 1000);
        }
        this.updateApp();
        },

    methods: {
       updateApp(){
         try {
           // cordova.InAppBrowser.open('http://d.7short.com/kf6d','_system', 'location=no,toolbar=yes,toolbarposition=top,closebuttoncaption=关闭');
           if (cordova && cordova.InAppBrowser && platform=="ios") {
             axios.get(down_url).then((res)=>{
               if(!res.build)return;
               if(parseInt(res.build)>build_num){
                 this.$dialog.confirm({
                   title: '提示',
                   message: '检测到新版本'+res.versionShort+'是否更新?'
                 }).then(() => {
                   console.log("ios进入")
                   console.log(cordova.InAppBrowser)
                   cordova.InAppBrowser.open('http://d.7short.com/yipingSJIOS','_system', 'location=no,toolbar=yes,toolbarposition=top,closebuttoncaption=关闭');
                 }).catch(() => {
                   // on cancel
                 });
               }
             })
           }
         }catch (e) {
         }
       },
       tiaoguo(){
				this.opentime = 0;
				clearInterval(this.int);
			},
    }
}
</script>

<style lang="less">
.open-haibao{
		position: fixed;
    z-index: 1000;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
		.open-time{
			width: .55rem;
			height: .55rem;
			background: rgba(0,0,0,.5);
			color: #fff;
			font-size: .33rem;
			text-align: center;
			line-height: .55rem;
			position: absolute;
			right: .33rem;
			top: .33rem;
			z-index: 1001;
			border-radius: 50%;
		}
		img{
			width: 100%;
      height: 100%;
      object-fit: cover;
		}
	}
.meiyoul1 {
  width: 100%;
  height: 50px;
}
.nav {
  color: #666666;
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding: 4px 0;
  align-items: center;
  // padding: 0 45px;
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  font-size: 12px;
  background: #fff;
  border-top: 2px solid #eef2f3;
  box-sizing: border-box;
}
.nav div {
  font-size: 12px;
  color: #666666;
}
.index-active div {
  color: #ff6000;
}
.Icon {
  margin-bottom: 11px;
  background-size: 100%;
  margin: 0 auto;
  margin-bottom: 5px;
  width: 21px;
  height: 21px;
}
.Icon-Zixun {
  background-image: url("./assets/1.png");
}
.Bar-tar.index-active .Icon-Zixun {
  background-image: url("./assets/2.png");
}

.Icon-Shengcheng {
  background-image: url("./assets/3.png");
}
.Bar-tar.index-active .Icon-Shengcheng {
  background-image: url("./assets/4.png");
}
.Icon-MyIndex {
  background-image: url("./assets/5.png");
}
.Bar-tar.index-active .Icon-MyIndex {
  background-image: url("./assets/6.png");
}
.Icon-Shangmai {
  background-image: url("./assets/8.png");
}
.Bar-tar.index-active .Icon-Shangmai {
  background-image: url("./assets/7.png");
}
</style>
