import { apiAxios,apiAxiosJson } from './axios.js'
//获取公告列表接口
export const queryNoticeList = (data) => {
    return apiAxios('get', '/api/notice/queryNoticeList', data)
}
//获取公告详情接口
export const getDetailById = (data) => {
    return apiAxios('get', '/api/notice/getDetailById', data)
}
//商户数据分析接口
export const getTaskStatistics = (data) => {
    return apiAxios('get', '/api/merchant/getTaskStatistics', data)
}

//获取发布指导
export const getIssuedGuidance = (data) => {
    return apiAxios('get', '/api/order/getIssuedGuidance', data)
}
//手机号删除接口
export const phoneDelete = (data) => {
    return apiAxiosJson('post', '/api/order/phoneDelete', data)
}
//手机号列表
export const queryPhoneOrderList = (data) => {
    return apiAxios('get', '/api/order/queryPhoneOrderList', data)
}
//商家发布手机号辅助任务（批量上传）
export const phonePublish = (data) => {
   
    return apiAxiosJson('post', 'api/order/phonePublish', data)
}
//商户确认任务辅助成功接口 需要token
export const phoneSuccess = (data) => {
    return apiAxios('post', 'api/order/success', data)
}
//商户确认任务辅助失败接口 需要token
export const phoneFail = (data) => {
    return apiAxios('post', 'api/order/fail', data)
}

