import {merchantPasswordLogin, vscode, merchantCodeLogin, merchantCodeRegister} from "@/api/login"
import {queryNoticeList} from "../../api";
import {getMerchantInfo, updateInfo} from "../../api/mine";

const state = {
    token: '',
    userInfo: {
        directCityArray: []
    },
    hasPassword: '',/*商户是否设置了密码，false否 true是，未设置密码跳转到设置密码界面*/
    directCity: "1"
}

const mutations = {
    /*verification_token*/
    SET_TOKEN: (state, token) => {
        state.token = token
    },
    SET_USERINFO: (state, userInfo) => {
        userInfo.balance = userInfo.balance / 100;
        userInfo.codePrice = userInfo.codePrice / 100;
        userInfo.apiPrice = userInfo.apiPrice / 100;
        userInfo.phonePrice = userInfo.phonePrice / 100;
        state.userInfo = userInfo
    },
    HAS_PASSWORD: (state, hasPassword) => {
        state.hasPassword = hasPassword
    },
    SET_DIRECTCITY: (state, directCity) => {
        state.directCity = directCity
    },
    SET_BALANCE: (state, balance) => {
        state.userInfo.balance = balance
    },

}

const actions = {
    /*获取用户信息*/
    async userinfos({commit}) {
        return new Promise((resolve, reject) => {
            getMerchantInfo().then(response => {
                const {code, data} = response
                if (code == 200) {
                    commit('SET_USERINFO', data)
                }
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })
    },
    // user 登录接口
    async login({commit}, data) {
        return new Promise((resolve, reject) => {
            merchantPasswordLogin(data).then(response => {
                const {code, data} = response
                if (code == 200) {
                    commit('SET_USERINFO', data.merchant)
                    commit('SET_TOKEN', data.token)
                    commit('HAS_PASSWORD', data.hasPassword)
                }
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })

    },
    /*验证码登录*/
    async login2({commit}, data) {
        return new Promise((resolve, reject) => {
            merchantCodeLogin(data).then(response => {
                const {code, data} = response
                if (code == 200) {
                    commit('SET_USERINFO', data.merchant)
                    commit('SET_TOKEN', data.token)
                    commit('HAS_PASSWORD', data.hasPassword)
                }
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })

    },
    /*注册登录*/
    async loginEnroll({commit}, data) {
        return new Promise((resolve, reject) => {
            merchantCodeRegister(data).then(response => {
                const {code, data} = response
                if (code == 200) {
                    commit('SET_USERINFO', data.merchant)
                    commit('SET_TOKEN', data.token)
                    commit('HAS_PASSWORD', data.hasPassword)
                }
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })

    },
    /*x修改商户资料接口*/
    async updateInfo2({dispatch}, datas) {
        return new Promise((resolve, reject) => {
            updateInfo(datas).then(response => {
                const {code} = response
                if (code == 200) {
                    dispatch('userinfos')
                }
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })

    },
    // 公告列表
    async noticeList() {
        return new Promise((resolve, reject) => {
            queryNoticeList().then(response => {
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })

    },
    /*设置是否定向*/
    set_directCity({commit}, data) {
        commit('SET_DIRECTCITY', data)
    },
    // 获取验证是否登录或者token过期
    getInfo({commit}) {
    },
    /*发送验证码*/
    async vCode({state}, data) {
        // let phone = data ||state.userInfo.phone
        let phone = data
        return new Promise((resolve, reject) => {
            vscode({phone: phone}).then(response => {
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })
    },
    /*退出登录*/
    logout({commit}) {
        commit('SET_USERINFO', '')
        commit('SET_TOKEN', '')
        commit('HAS_PASSWORD', '')
    },
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}

