import {city, province} from "@/api/city";

const state = {
    province: [],
    city: [],
}

const mutations = {
    SET_PROVINCE: (state, province) => {
        state.province = province
    },
    SET_CITY: (state, city) => {
        state.city = city
    },


}

const actions = {
    /*获取省*/
    async get_city({commit}, data) {
        return new Promise((resolve, reject) => {
            city(data).then(response => {
                const {code, data} = response
                if (code == 200) {
                    commit('SET_CITY', data)
                }
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })

    },
    /*获取市*/
    async get_province({commit}, data) {
        return new Promise((resolve, reject) => {
            province(data).then(response => {
                const {code, data} = response
                if (code == 200) {
                    commit('SET_PROVINCE', data)
                }
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })

    },
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}

