import axios from 'axios'
import qs from 'qs'
import Router from '@/router.js'
import store from '@/store'
import {Toast} from 'vant'
//axios.defaults.timeout = 60000;           //超时时间
// axios.defaults.retry = 3;                 //请求次数
// axios.defaults.retryDelay = 1000;         //请求间隙
let baseURL = process.env.VUE_APP_API_Url


// 在config.js文件中统一存放一些公共常量，方便之后维护

let Token;
// 添加请求拦截器，在发送请求之前做些什么(**具体查看axios文档**)--------------------------------------------
axios.interceptors.request.use(function (config) {
    return config
}, function (error) {
    // 请求错误时弹框提示，或做些其他事

    return Promise.reject(error)
})

// 添加响应拦截器(**具体查看axios文档**)----------------------------------------------------------------
axios.interceptors.response.use(function (response) {

    // 对响应数据做点什么，允许在数据返回客户端前，修改响应的数据
    // 如果只需要返回体中数据，则如下，如果需要全部，则 return response 即可
    /*判断如果不是200直接返回登录界面*/
    if (response.data.code == 401 ) {
        store.commit('user/SET_TOKEN','')
        Router.replace({name: 'login'})
    }
    if(response.data.code == 500){
        Toast(response.data.msg)
    }
    return response.data
}, function (error) {
    // 对响应错误做点什么
    return Promise.reject(error)
})

// 封装数据返回失败提示函数---------------------------------------------------------------------------
function errorState(response) {

    // 隐藏loading
    // 如果http状态码正常，则直接返回数据
    if (response && (response.status === 200 || response.status === 304 || response.status === 400)) {
        // 如果不需要除了data之外的数据，可以直接 return response.data
        return response
    } else {


    }
}

// 封装数据返回成功提示函数---------------------------------------------------------------------------
function successState(res) {
    // 隐藏loading
    // 统一判断后端返回的错误码(错误码与后台协商而定)
    if(res.code == '-1'){
        Toast(res.msg)
        setTimeout(()=>{
            Router.replace({name: 'login'})
        },1500)
        return false;
    }
    if (res.errno === -1) {
        alert('success')
        return res
    }
}

function getParam(data) {
    let url = '';
    for (var k in data) {
        let value = data[k] !== undefined ? data[k] : '';
        url += `&${k}=${encodeURIComponent(value)}`
    }
    return url ? url.substring(1) : ''
}

//最终获取带参数的url方法
function newUrl(url, data) {
    //看原始url地址中开头是否带?，然后拼接处理好的参数
    return url += (url.indexOf('?') < 0 ? '?' : '') + getParam(data)
}

// 封装axios--------------------------------------------------------------------------------------
export const apiAxios = (method, url, params = {}, headers = {}, baseURL = process.env.VUE_APP_API_Url) => {
    if (store.state.user.token) {
        // params['token'] = store.state.user.token || ''
        headers['token'] = store.state.user.token || ''
    }
    headers['Content-Type'] = 'application/json'
    let httpDefault = {
        method: method,
        baseURL: baseURL,
        url: newUrl(url, params),
        headers: headers,
        // withCredentials: true,
        // `params` 是即将与请求一起发送的 URL 参数
        // `data` 是作为请求主体被发送的数据

        // data: qs.stringify(params)
    }

    // 注意**Promise**使用(Promise首字母大写)
    return new Promise((resolve, reject) => {
        axios(httpDefault)
        // 此处的.then属于axios
            .then((res) => {

                successState(res)
                resolve(res)
            }).catch((response) => {

            errorState(response)
            reject(response)
        })
    })
}
export const apiAxiosJson = (method, url, params = {}, headers = {}, baseURL = process.env.VUE_APP_API_Url) => {
  
    if (store.state.user.token) {
        // params['token'] = store.state.user.token || ''
        headers['token'] = store.state.user.token || ''
    }
    headers['Content-Type'] = 'application/json'
    console.log(params)
    let httpDefault = {
        method: method,
        headers:headers,
        baseURL: baseURL,
        url: url,
        data: JSON.stringify(params)
        
    }

    // 注意**Promise**使用(Promise首字母大写)
    return new Promise((resolve, reject) => {
        axios(httpDefault)
        // 此处的.then属于axios
            .then((res) => {

                successState(res)
                resolve(res)
            }).catch((response) => {

            errorState(response)
            reject(response)
        })
    })
}
//form-data 图片上传
export const upAxios = (method, url, params = {},headers={}, baseURL = process.env.VUE_APP_API_Url) => {
    let param = new FormData(); // 创建form对象
    // params['access_token'] = '';
    if (store.state.user.token) {
        headers['token'] = store.state.user.token || ''
    }
    console.log(params)
    param.append('file', params); // 通过append向form对象添加数据
    let httpDefault = {
        method: method,
        headers:headers,
        baseURL: baseURL,
        url: url,
        data: param
    }

    // 注意**Promise**使用(Promise首字母大写)
    return new Promise((resolve, reject) => {
        axios(httpDefault)
        // 此处的.then属于axios
            .then((res) => {
                successState(res)
                resolve(res)
            }).catch((response) => {
            errorState(response)
            reject(response)
        })
    })
}
