import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store/index'
import './util/Css/util.css';
import 'vant/lib/index.css';
Vue.config.productionTip = false
// import 'default-passive-events'
import VideoPlayer from 'vue-video-player'
Vue.use(VideoPlayer)
Vue.config.devtools = true
import 'lib-flexible'
import Vantui from './Vant/Vant.js'
import './util/Less/util.less'
import * as filters from './filters'
import './util/Less/bill_index.less'
import './util/Less/Personal_index.less'
import './util/Less/ChangePassword.less'

Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})
// 全局组件自动注册
// import '@/util/index'
Vantui.VantuiInit(Vue)
Vue.prototype.getUrlParam = function (name) {
  let reg = new RegExp("(^|\\?|&)" + name + "=([^&]*)(\\s|&|$)","i");
  if(reg.test(window.location.href)){
      return unescape(RegExp.$2.replace(/\+/g," "))
  }
  return undefined
},
Vue.prototype.$echarts = echarts
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
