import { apiAxios,apiAxiosJson,upAxios } from './axios.js'
//验证码
export const vscode = (data) => {
    return apiAxios('post', 'api/sms/send', data)
}

//商户验证码登录接口

export const merchantCodeLogin = (data) => {
    return apiAxios('post', 'api/login/merchantCodeLogin', data)
}
//手机号密码登录接口
export const merchantPasswordLogin = (data) => {
    return apiAxios('post', 'api/login/merchantPasswordLogin', data)
}
//手机号密码登录接口
export const merchantCodeRegister = (data) => {
    return apiAxios('post', 'api/login/merchantCodeRegister', data)
}
export const merchantPartnerCodeRegister = (data) => {
    return apiAxios('post', 'api/login/merchantPartnerCodeRegister', data)
}

//设置转移密码接口
export const updatePayPassword = (data) => {
    return apiAxios('post', 'api/merchant/updatePayPassword', data)
}
///忘记密码接口
export const forgetPassword = (data) => {
    return apiAxios('post', 'api/merchant/forgetPassword', data)
}
