//余额明细查询接口
export const queryMyBalanceList = (data) => {
    return apiAxios('get', 'api/merchant/queryMyBalanceList', data)
}


//商家发布二维码辅助任务接口
export const codePublish = (data) => {
    return apiAxiosJson('post', 'api/order/codePublish', data)
}
export const getConfig = (data) => {
    return apiAxiosJson('get', 'api/system/getConfig', data)
}
//上传图片
export const uploadFile = (data) => {
    return upAxios('post', 'api/file/uploadFile', data)
}


//获取验证码
export const sendByMerchantToken = (data) => {
	return apiAxios('post', '/api/sms/sendByMerchantToken', data)
}
