import { Area, ActionSheet, Stepper, Tab, Tabbar, TabbarItem, Switch, Tabs, List, Cell, PullRefresh, Toast, Progress, Field, Button, Popup, NavBar, Tag, DatetimePicker, Sticky, Image, Icon, Picker, Notify, Dialog, CellGroup, Uploader, RadioGroup, Radio, DropdownMenu, DropdownItem, Swipe, SwipeItem, Checkbox, CheckboxGroup, Circle, SwipeCell, Grid, GridItem, NumberKeyboard, PasswordInput, CountDown, Loading, Overlay } from 'vant';
// import LyTab from '../ly-tab';
class Vantui {
    static VantuiInit(Vue) {
        this.Vue = Vue;
        this.ArryUi = [Area, ActionSheet, Tabbar, TabbarItem, Stepper, Button, Switch, Field, Tab, Tabs, List, Cell, PullRefresh, Toast, Progress, Tag, Popup, NavBar, DatetimePicker, Sticky, Image, Icon, Picker, Notify, Dialog, CellGroup, Uploader, RadioGroup, Radio, DropdownMenu, DropdownItem, Swipe, SwipeItem, Checkbox, CheckboxGroup, Circle, SwipeCell, Grid, GridItem, NumberKeyboard, PasswordInput, Overlay, CountDown, Loading, Overlay];

        this.ArryVantui()
    }
    static ArryVantui() {
        Vantui.ArryUi.forEach(res => {
            Vantui.Vue.use(res);
        })

        // Vantui.Vue.use(LyTab);
    }



}

export default Vantui