// index 10000开始登录层��

import Vue from 'vue'
import Router from 'vue-router'
import store from "./store";
// import Home from './views/Home.vue'

Vue.use(Router)
let router = new Router({
    routes: [
        {
            // 启动路径
            path: '/',
            redirect: '/publish'
        },

        {
            path: '/susss',
            name: 'susss',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: () => import(/* webpackChunkName: "about" */ './views/Index/index.vue'),
            meta: {index: 10000, showFooter: true},
        },
        /*登录*/
        {
            path: '/login',
            name: 'login',
            component: () => import(/* webpackChunkName: "about" */ './views/yu/login/index.vue'),
            meta: {index: 10000,},
        },
        /*验证码登录*/
        {
            path: '/loginVerification',
            name: 'loginVerification',
            component: () => import(/* webpackChunkName: "about" */ './views/yu/login/loginVerification.vue'),
            meta: {index: 10000,},
        },
        /*设置密码*/
        {
            path: '/setPassword',
            name: 'setPassword',
            component: () => import(/* webpackChunkName: "about" */ './views/yu/login/setPassword.vue'),
            meta: {index: 10000,},
        },
        /*忘记密码*/
        {
            path: '/forgetPwd',
            name: 'forgetPwd',
            component: () => import(/* webpackChunkName: "about" */ './views/yu/login/forgetPwd.vue'),
            meta: {index: 10000,},
        },
        /*发布页*/
        {
            path: '/publish',
            name: 'publish',
            component: () => import(/* webpackChunkName: "about" */ './views/yu/publish/index.vue'),
            meta: {index: 10000, showFooter: true},
        },
        /*数据分析*/
        {
            path: '/analysis',
            name: 'analysis',
            component: () => import(/* webpackChunkName: "about" */ './views/yu/analysis/index.vue'),
            meta: {index: 10000, showFooter: true},
        },
        {
            path: '/phoneUp',
            name: 'phoneUp',
            component: () => import(/* webpackChunkName: "about" */ './views/yu/publish/phoneUp.vue'),
            meta: {index: 10000, keep: false},
        },
        /*余额明细*/
        {
            path: '/balance',
            name: 'balance',
            component: () => import(/* webpackChunkName: "about" */ './views/yu/balance/index.vue'),
            meta: {index: 10000, keep: false},
        },
        /*余额转移*/
        {
            path: '/transfer',
            name: 'transfer',
            component: () => import(/* webpackChunkName: "about" */ './views/yu/balance/transfer.vue'),
            meta: {index: 10000, keep: false},
        },
        {
            path: '/transferPwd',
            name: 'transferPwd',
            component: () => import(/* webpackChunkName: "about" */ './views/yu/balance/transferPwd.vue'),
            meta: {index: 10000, keep: false},
        },
        {
            path: '/transferDetail',
            name: 'transferDetail',
            component: () => import(/* webpackChunkName: "about" */ './views/yu/balance/transferDetail.vue'),
            meta: {index: 10000, keep: false},
        },
        {
            path: '/bill',
            name: 'bill',
            component: () => import(/* webpackChunkName: "about" */ './Dpage/bill/index.vue'),
            meta: {index: 10000, showFooter: true},
        },
        {
            path: '/Personal/index',
            name: 'Personal',
            component: () => import(/* webpackChunkName: "about" */ './Dpage/Personal/index.vue'),
            meta: {index: 10000, showFooter: true},
        },
        {
            path: '/Personal/Recharge',
            name: 'Recharge',
            component: () => import(/* webpackChunkName: "about" */ './Dpage/Personal/Recharge.vue'),
            meta: {index: 10000, keep: false},
        },
        /*下级*/
        {
            path: '/Personal/Subordinate',
            name: 'Subordinate',
            component: () => import(/* webpackChunkName: "about" */ './Dpage/Personal/Subordinate.vue'),
            meta: {index: 10000, keep: false},
        },
        {
            path: '/Personal/information',
            name: 'Personal',
            component: () => import(/* webpackChunkName: "about" */ './Dpage/Personal/information.vue'),
            meta: {index: 10000, keep: false},
        }, {
            path: '/Personal/ChangePassword',
            name: 'ChangePassword',
            component: () => import(/* webpackChunkName: "about" */ './Dpage/Personal/ChangePassword.vue'),
            meta: {index: 10000, keep: false},
        },
        {
            path: '/Personal/recommend',
            name: 'recommend',
            component: () => import(/* webpackChunkName: "about" */ './Dpage/Personal/recommend.vue'),
            meta: {index: 10000, keep: false},
        },
        {
            path: '/Personal/Dataquery',
            name: 'Dataquery',
            component: () => import(/* webpackChunkName: "about" */ './Dpage/Personal/Dataquery.vue'),
            meta: {index: 10000, keep: false},
        }, {
            path: '/Personal/download',
            name: 'download',
            component: () => import(/* webpackChunkName: "about" */ './Dpage/Personal/download.vue'),
            meta: {index: 10000, keep: false},
        },
        {
            path: '/Personal/opinion/index',
            name: 'opinionIndex',
            component: () => import(/* webpackChunkName: "about" */ './Dpage/Personal/opinion/index.vue'),
            meta: {index: 10000, keep: false},
        },
        {
            path: '/Personal/opinion/list',
            name: 'opinionList',
            component: () => import(/* webpackChunkName: "about" */ './Dpage/Personal/opinion/list.vue'),
            meta: {index: 10000, keep: false},
        },
    ],

    linkActiveClass: 'index-active'
})
router.beforeEach((to, from, next) => {
    if (to.matched.length === 0) { //如果未匹配到路由
        from.name ? next({
            name: from.name
        }) : next('/'); //如果上级也未匹配到路由则跳转登录页面，如果上级能匹配到则转上级路由
    } else if (store.state.user.token != '' && to.name == 'login') {
        next('/');
    } else {
        // 未登陆下只能跳转首页，登录注册页面 || to.name == 'index'
        if (store.state.user.token != '' || to.name == 'login' || to.name == 'forgetPwd') {
            next()
            //如果匹配到正确跳转
        } else {
            // next()
            next('/login')
        }
    }
});
router.afterEach((to) => {
    window.scrollTo(0, 0);
});
export default router