let dateServer = value => {
    return value.replace(/(\d{4})(\d{2})(\d{2})/g, '$1-$2-$3')
}
let ksum = value => {
    if (value >= 1000) {
        return value / 1000 + "k";
    }
    return value;
}
let before_time = date1 => {
    date1 = new Date(date1).getTime();
    let minute = 1000 * 60; //把分，时，天，周，半个月，一个月用毫秒表示
    let hour = minute * 60;
    let day = hour * 24;
    let week = day * 7;
    let halfamonth = day * 15;
    let month = day * 30;
    let year = day * 365;
    let now = new Date().getTime(); //获取当前时间毫秒
    // console.log(now);
    let diffValue = now - date1; //时间差

    if (diffValue < 0) {
        return;
    }
    let Y = new Date(date1).getFullYear();
    let M = new Date(date1).getMonth() + 1;
    let D = new Date(date1).getDate();
    let H = new Date(date1).getHours();
    let S = new Date(date1).getMinutes();
    let minC = diffValue / minute; //计算时间差的分，时，天，周，月
    let hourC = diffValue / hour;
    let dayC = diffValue / day;
    let weekC = diffValue / week;
    let monthC = diffValue / month;
    let yearC = diffValue / year;
    let result;
    if (yearC >= 1) {
        result = Y + "年" + M + "月" + D + "日";
    } else if (monthC >= 1 && monthC <= 12) {
        result = " " + parseInt(monthC) + "月前";
    } else if (weekC >= 1 && weekC <= 4) {
        result = " " + parseInt(weekC) + "周前";
    } else if (dayC >= 1 && dayC <= 7) {
        result = " " + parseInt(dayC) + "天前";
    } else if (hourC >= 1 && hourC <= 24) {
        result = " " + parseInt(hourC) + "小时前";
    } else if (minC >= 1 && minC <= 60) {
        result = " " + parseInt(minC) + "分钟前";
    } else if (diffValue >= 0 && diffValue <= minute) {
        result = `${H}:${S}`;
    } else {
        let datetime = new Date();
        datetime.setTime(date1);
        let Nyear = datetime.getFullYear();
        let Nmonth =
            datetime.getMonth() + 1 < 10
                ? "0" + (datetime.getMonth() + 1)
                : datetime.getMonth() + 1;
        let Ndate =
            datetime.getDate() < 10
                ? "0" + datetime.getDate()
                : datetime.getDate();
        let Nhour =
            datetime.getHours() < 10
                ? "0" + datetime.getHours()
                : datetime.getHours();
        let Nminute =
            datetime.getMinutes() < 10
                ? "0" + datetime.getMinutes()
                : datetime.getMinutes();
        var Nsecond =
            datetime.getSeconds() < 10
                ? "0" + datetime.getSeconds()
                : datetime.getSeconds();
        result = Nyear + "-" + Nmonth + "-" + Ndate;
    }
    return result;
}
export { dateServer, ksum, before_time }