import { apiAxios,apiAxiosJson } from './axios.js'
//获取商户资料接口
export const getMerchantInfo = (data) => {
    return apiAxios('post', '/api/merchant/getMerchantInfo', data)
}
//修改商户资料接口
export const updateInfo = (data) => {
    return apiAxios('post', '/api/merchant/updateInfo', data)
}

//修改密码接口 需要token
export const updatePassword = (data) => {
    return apiAxios('post', '/api/merchant/updatePassword', data)
}
//获取下级
export const queryDirectDownMerchantList = (data) => {
    return apiAxios('get', '/api/merchant/queryDirectDownMerchantList', data)
}
//转移金额
export const transferBalance = (data) => {
    return apiAxios('post', '/api/merchant/transferBalance', data)
}
/*用户数据查询*/
export const statisticsSearch = (data) => {
    return apiAxios('post', '/api/merchant/statisticsSearch', data)
}
/*转移支出查询接口*/
export const queryTransferPayOffList = (data) => {
    return apiAxios('post', '/api/merchant/queryTransferPayOffList', data)
}
/*转移收入查询接口*/
export const queryTransferIncomeList = (data) => {
    return apiAxios('post', '/api/merchant/queryTransferIncomeList', data)
}

/*获取邀请码*/
export const getInviteCode = (data) => {
    return apiAxios('post', '/api/merchant/getInviteCode', data)
}
/*商户充值记录*/
export const queryTopUpList = (data) => {
    return apiAxios('post', '/api/merchant/queryTopUpList', data)
}


//意见反馈接口 需要token
export const declareIdea = (data) => {
	return apiAxios('post', '/api/merchant/declareIdea', data)
}
//意见反馈接口详情
export const getOpinion = (data) => {
	return apiAxios('post', '/api/idea/getIdeaInfo', data)
}
//意见反馈接口列表
export const getOpinionList = (data) => {
	return apiAxios('post', '/api/idea/queryMerchantIdeaListByType', data)
}


